.lang {
	display: flex;
	align-items: center;
	//@include adaptive(767) {
	//	position: absolute;
	//	top: 25px;
	//	right: 15px;
	//}
	//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
	//	position: absolute;
	//	top: 25px;
	//	right: 15px;
	//}

	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 30px;
		margin-left: 5px;
		margin-right: 5px;
		border-radius: 50%;
		background-color: #dbd1b6;
		color: #ffffff;
		font-size: 14px;
		font-family: 'Frankfurter', sans-serif;
		@include transition(background-color);

		&:first-of-type {
			margin-left: 0;
		}
		&:last-of-type {
			margin-right: 0;
		}

		&:hover {
			background-color: rgba(#41ad4a, 0.75);
			@include transition(background-color);
			//@include adaptive(767) {
			//	background-color: #dbd1b6;
			//}
		}

		&:active {
			@include adaptive(767) {
				background-color: rgba(#41ad4a, 0.75);
				@include transition(background-color);
			}
		}

		&.is-active {
			background-color: #41ad4a;
			@include transition(background-color);

			&:hover {
				@include adaptive(767) {
					background-color: #41ad4a;
				}
			}
		}
	}
}

.link {
	display: flex;
	align-items: center;
	//@include adaptive(767) {
	//	flex-wrap: wrap;
	//}
	//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
	//	flex-wrap: wrap;
	//}

	&__btn {
		overflow: hidden;
		position: relative;
		color: #006023;
		font-size: 14px;
		@include adaptive(1023) {
			font-size: 12px;
			text-align: left;
		}
		//@include adaptive(639) {
		//	color: #ffffff;
		//}
		//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
		//	color: #ffffff;
		//}

		&-1 {
			//@include adaptive(767) {
			//	width: 100%;
			//}
			//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
			//	width: 100%;
			//}
		}

		&:hover {
			&:after {
				transform: translateY(-3px);
				@include transition(transform);
			}
		}

		&:after {
			content: '';
			position: absolute;
			top: 100%;
			transform: translateY(100%);
			left: 0;
			width: 100%;
			height: 1px;
			background-color: #006023;
			@include transition(transform);
		}
	}

	span {
		margin-left: 10px;
		margin-right: 10px;
		color: #006023;
		//@include adaptive(639) {
		//	color: #ffffff;
		//}
		//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
		//	color: #ffffff;
		//}
	}
}

.vers {
	display: flex;

	&__name {
		display: flex;
		align-items: center;
		font-size: 18px;
		color: #006023;
		@include adaptive(1023) {
			font-size: 16px;
		}
		//@include adaptive(767) {
		//	flex-direction: column;
		//	align-items: flex-end;
		//	font-size: 12px;
		//	text-align: right;
		//}
		//@include adaptive(639) {
		//	color: #ffffff;
		//}
		//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
		//	flex-direction: column;
		//	align-items: flex-end;
		//	font-size: 12px;
		//	text-align: right;
		//	color: #ffffff;
		//}

		span {
			font-weight: bold;
		}

		a {
			overflow: hidden;
			position: relative;
			margin-left: 5px;
			color: #006023;
			//@include adaptive(639) {
			//	color: #ffffff;
			//}
			//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
			//	color: #ffffff;
			//}

			&:hover {
				&:after {
					transform: translateY(100%);
					@include transition(transform);
				}
			}

			&:after {
				content: '';
				position: absolute;
				top: 100%;
				left: 0;
				transform: translateY(-4px);
				width: 100%;
				height: 1px;
				background-color: #006023;
				@include transition(transform);
				//@include adaptive(639) {
				//	background-color: #ffffff;
				//}
				//@media screen and (orientation: landscape) and (max-width: 812px) and (max-height: 414px) {
				//	background-color: #ffffff;
				//}
			}
		}
	}
}

.cookie {
	z-index: 1000;
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: space-between;
	bottom: 0;
	left: 0;
	width: 100%;
	min-height: 130px;
	padding: 15px 50px;
	background-color: rgba(#235f25, 0.90);
	//@include adaptive(767) {
	//	padding-left: 30px;
	//	padding-right: 30px;
	//}
	//@include adaptive(639) {
	//	flex-direction: column;
	//	align-items: center;
	//	justify-content: center;
	//}

	&__left {
		flex: 1;
		min-width: 1px;
		max-width: 100%;
		padding-right: 15px;
		//@include adaptive(639) {
		//	padding-right: 0;
		//}
	}

	&__right {
		display: flex;
		justify-content: flex-end;
		width: 100%;
		max-width: 165px;
		//@include adaptive(639) {
		//	justify-content: center;
		//}
	}

	p {
		width: 100%;
		max-width: 890px;
		padding-right: 15px;
		color: #ffffff;
		font-size: 18px;
		//@include adaptive(639) {
		//	font-size: 16px;
		//}
		//@include adaptive(639) {
		//	text-align: center;
		//	padding-bottom: 15px;
		//}
	}

	a {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		max-width: 165px;
		height: 53px;
		border-radius: 27px;
		border: 2px solid #fff;
		background-color: #ffffff;
		color: #235f25;
		font-size: 26px;
		text-transform: uppercase;
		font-family: 'Frankfurter', sans-serif;
		@include transition(background-color, color);
		//@include adaptive(639) {
		//	font-size: 22px;
		//	max-width: 135px;
		//	height: 43px;
		//}

		&:hover {
			background-color: transparent;
			color: #ffffff;
			@include transition(background-color, color);
		}
	}
}

